import langEn from '../utils/lang/en-us'
import {getLangPkg} from '../utils/langPkg'

const metaMaskInfo = {
  data () {
    return {
      metaInfo: {
        info: langEn.commom.info,
        warning: langEn.commom.warning,
        success: langEn.commom.success,
        error: langEn.commom.error,
        status1: langEn.promptMessage.metaStatus1,
        status2: langEn.promptMessage.metaStatus2,
        status3: langEn.promptMessage.metaStatus3,
        status4: langEn.promptMessage.metaStatus4,
        status5: langEn.promptMessage.metaStatus5,
        networkVersion: langEn.promptMessage.networkVersion,
        recommendedAddress: langEn.promptMessage.recommendedAddress,
        checkAddress: langEn.promptMessage.checkAddress,
        checkVal: langEn.promptMessage.checkVal,
        checkFloat: langEn.promptMessage.checkFloat,
        loginWallet: langEn.promptMessage.loginWallet,
        loadBalance: langEn.promptMessage.loadBalance,
        checkBalance: langEn.promptMessage.checkBalance,
        isSupported: langEn.promptMessage.isSupported,
        supportedTime: langEn.promptMessage.supportedTime,
        createAddress: langEn.promptMessage.createAddress,
        copySuccess: langEn.promptMessage.copySuccess,
        copyFail: langEn.promptMessage.copyFail,
        infoAddress: langEn.promptMessage.infoAddress
      }
    }
  },
  watch: {
    '$i18n.locale': {
      handler: function (newVal) {
        const lang = getLangPkg(newVal)
        const commom = lang.commom
        const promp = lang.promptMessage
        this.metaInfo.info = commom.info
        this.metaInfo.warning = commom.warning
        this.metaInfo.success = commom.success
        this.metaInfo.error = commom.error
        this.metaInfo.status1 = promp.metaStatus1
        this.metaInfo.status2 = promp.metaStatus2
        this.metaInfo.status3 = promp.metaStatus3
        this.metaInfo.status4 = promp.metaStatus4
        this.metaInfo.status5 = promp.metaStatus5
        this.metaInfo.networkVersion = promp.networkVersion
        this.metaInfo.recommendedAddress = promp.recommendedAddress
        this.metaInfo.checkAddress = promp.checkAddress
        this.metaInfo.checkVal = promp.checkVal
        this.metaInfo.checkFloat = promp.checkFloat
        this.metaInfo.loginWallet = promp.loginWallet
        this.metaInfo.loadBalance = promp.loadBalance
        this.metaInfo.checkBalance = promp.checkBalance
        this.metaInfo.isSupported = promp.isSupported
        this.metaInfo.supportedTime = promp.supportedTime
        this.metaInfo.createAddress = promp.createAddress
        this.metaInfo.copySuccess = promp.copySuccess
        this.metaInfo.copyFail = promp.copyFail
        this.metaInfo.infoAddress = promp.infoAddress
      },
      immediate: true
    }
  }
}
export {metaMaskInfo}



