// 进入['Tasks', 'Withdraw', 'Statistics', 'MasterNode']页面时，过滤直推地址的合法性
import store from '../store/index'
const urlFilter = {
  // 全局导航守卫不可能过来 fromParam为真toParam为假的数据
  beforeRouteEnter (to, from, next) {
    const fromQuery = from.query
    const fromParam = fromQuery.to
    const toParam = to.query.to
    // todo 8.07, 如果用户实际拥有上级邀请地址，则直接通过
    const straightAddress = store.state.metamask.straightAddress
    if (toParam && toParam.length === 42) {
      if (store.state.metamask.urlStatus !== 1) { // urlStatus !== 1 时，提交改变状态
        store.commit('metamask/setUrlStatus', 1)
      }
      next()
    } else if (fromParam && fromParam.length === 42) {
      next({
        path: to.path,
        query: fromQuery
      })
    } else if (straightAddress !== null && straightAddress !== '0x0000000000000000000000000000000000000000') {
      if (store.state.metamask.urlStatus !== 0) { // urlStatus === 1 时，提交改变状态
        store.commit('metamask/setUrlStatus', 0)
      }
      next()
    } else {
      // todo 提交一个状态，以便提示用户的直推地址不合法
      if (store.state.metamask.urlStatus !== 0) { // urlStatus === 1 时，提交改变状态
        store.commit('metamask/setUrlStatus', 0)
      }
      next({
        path: '/purchase'
      })
    }
  }
}

export {urlFilter}



