import store from '../store/index'
const checkUrlParam = {
  methods: {
    checkUrlParam () {
      const straightAddress = store.state.metamask.straightAddress
      if (straightAddress !== null && straightAddress !== '0x0000000000000000000000000000000000000000') {
        return straightAddress
      }
      const referralAddress = this.$route.query.to
      // const myWeb3 = Vue.prototype.$myWeb3 // 以metamask的提供器，获取web3对象
      // 浏览器会忽略#标志后面的数据，所以vue如果使用hash路由模式，则location.search获取不到值,需要启用history模式;但是可以通过this.#route.query参数获取
      if (referralAddress && referralAddress.length === 42) {
        return referralAddress
      }
      return false
    }
  }
}

export {checkUrlParam}



